.header{
    // border: 1px black solid;
    display: flex;
    text-align: center;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    padding: 30px;
    box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.1);

    h1{
        font-weight: bold;
    }
    p{
        font-size: 60px;
        font-weight: bold;
    }

  

    .header-image{
        img{
            border-radius: 200px;
            max-width: 300px;
            box-shadow: 2px 2px 8px 3px rgba(0, 0, 0, 0.25);

        }
    }
}

@media only screen and (min-width: 600px) {
    /* For tablets: */
    .header{
        align-items: center;
        justify-content: center;

    }
  }